<template>
  <v-app-bar app dark class="white primary--text">
    <v-app-bar-nav-icon
      class="primary--text"
      @click.stop="drawerLocal = !drawerLocal"
    />

    <v-toolbar-title
      justify="center"
      @mouseover="hoverTitle"
      @mouseleave="leaveTitle"
    >
      <router-link
        to="/"
        class="d-flex flex-nowrap"
        style="text-decoration: none"
      >
        <div class="d-none d-sm-flex">보라매&nbsp;&nbsp;</div>
        <v-icon class="primary--text">{{ titleIcon }}</v-icon>
        <div>센트럴바움</div>
      </router-link>
    </v-toolbar-title>
    <v-spacer />
    <v-btn
      outlined
      class="primary--text"
      @mouseover="hoverAccount"
      @mouseleave="leaveAccount"
      to="/interest"
    >
      <v-icon class="primary--text">{{ accountIcon }}</v-icon
      ><span class="d-none d-sm-flex pl-2">관심고객 등록</span>
    </v-btn>
    <v-btn outlined class="ml-2 primary--text" href="tel:02-849-9015">
      <v-icon class="primary--text">mdi-face-agent</v-icon
      ><span class="d-none d-sm-flex pl-2">02-849-9015</span>
    </v-btn>
  </v-app-bar>
</template>

<script>
export default {
  props: {
    drawer: Boolean
  },
  model: {
    prop: "drawer",
    event: "changeDrawer"
  },
  computed: {
    drawerLocal: {
      get: function() {
        return this.drawer;
      },
      set: function(value) {
        this.$emit("changeDrawer", value);
      }
    }
  },
  data() {
    return {
      titleIcon: "mdi-cube-outline",
      accountIcon: "mdi-account-plus-outline"
    };
  },
  methods: {
    hoverTitle: function() {
      this.titleIcon = "mdi-cube";
    },
    leaveTitle: function() {
      this.titleIcon = "mdi-cube-outline";
    },
    hoverAccount: function() {
      this.accountIcon = "mdi-account-plus";
    },
    leaveAccount: function() {
      this.accountIcon = "mdi-account-plus-outline";
    }
  }
};
</script>



