<template>
  <v-app id="inspire">
    <nav-drawer :menus="menus" :drawer="drawer" v-model="drawer" />

    <!-- <app-bar v-model="drawer" /> -->

    <v-app-bar
      app
      flat
      dark
      class="white primary--text justify--space-between mr-0"
    >
      <v-app-bar-nav-icon
        class="primary--text"
        @click.stop="drawer = !drawer"
        v-if="$vuetify.breakpoint.mdAndDown"
      />

      <v-toolbar-title class="align-left">
        <router-link
          to="/"
          class="d-flex flex-nowrap"
          style="text-decoration: none"
        >
          <v-img
            src="https://yaglobal.cafe24.com/boramea/images/logo.jpg"
            max-height="45"
          />
        </router-link>
      </v-toolbar-title>
      <v-spacer v-if="$vuetify.breakpoint.lgAndUp" />
      <v-row justify="end" v-if="$vuetify.breakpoint.lgAndUp">
        <v-menu
          v-model="menu"
          :close-on-content-click="true"
          offset-y
          open-on-hover
          transition="slide-x-transition"
        >
          <template v-slot:activator="{ on }">
            <div v-on="on">
              <v-btn
                class="white primary--text ma-2 title highlight-btn"
                dark
                tile
                text
                v-for="(menu, index) in menus"
                :key="index"
                >{{ menu.title }}</v-btn
              >
            </div>
          </template>

          <v-card fluid class="menu-container">
            <v-container fluid class="menus menu-container">
              <v-row class="menu-container text-left pa-0 ma-0">
                <v-col
                  md="2"
                  v-for="(menu, index) in menus"
                  :key="index"
                  class="menus text-left"
                >
                  <v-list class="justify-start menus pl-0">
                    <v-list-item
                      v-for="(submenu, index) in menu.submenus"
                      :key="index"
                      class="menus justify-start pa-0"
                    >
                      <v-btn
                        text
                        small
                        color="primary"
                        :class="submenu.class"
                        :to="submenu.path"
                      >
                        <v-icon>{{ submenu.icon }}</v-icon>
                        {{ submenu.title }}
                        <v-spacer />
                      </v-btn>
                    </v-list-item>
                  </v-list>
                  <!-- <v-divider vertical></v-divider> -->
                </v-col>
              </v-row>
            </v-container>
          </v-card>
        </v-menu>
      </v-row>
      <v-spacer />
      <div class="d-flex flex-nowrap mr-0 pr-0">
        <a
          class="d-flex flex-nowrap"
          style="text-decoration: none"
          href="tel:02-535-8450"
        >
          <!-- <v-img src="../assets/images/contact.jpg" max-height="50" /> -->
          <v-img
            class="align-end"
            src="https://yaglobal.cafe24.com/boramea/images/contact.jpg"
            max-width="160"
          />
        </a>
      </div>
    </v-app-bar>

    <!-- <v-content>
      <v-bottom-navigation
        :value="activeBtn"
        horizontal
        v-show="!$vuetify.breakpoint.smAndDown"
      >
        <v-menu v-for="(menu,index) in menus" :key="index" open-on-hover offset-y transition="slide-y-transition">
          <template v-slot:activator="{ on }">
              <v-btn v-on="on" class="subtitle-1">
                {{menu.title}}
              </v-btn>
          </template>
          <v-list>
            <v-list-item
              v-for="(submenu, index) in menu.submenus"
              :key="index"
              :to="submenu.path"
            >
              <v-list-item-title class="subtitle-2">{{ submenu.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-bottom-navigation>
    </v-content> -->

    <!-- <template v-if="$route.meta && $route.meta.fullLayout">
       -->
    <template v-if="$route.meta && $route.meta.fullLayout">
      <div>
        <v-container fluid class="pa-0">
          <div class="col-md-12 col-sm-12 col-xs-12 pa-0">
            <router-view />
          </div>
        </v-container>
      </div>
    </template>
    <template v-else>
      <div>
        <base-header
          :title="$route.meta.title"
          :mainImage="require('../assets/images/bg.jpg')"
        />
        <v-container fluid class="pa-0">
          <div class="col-md-12 col-sm-12 col-xs-12">
            <router-view />
          </div>
        </v-container>
      </div>
    </template>
    <base-footer />
  </v-app>
</template>

<script>
import BaseHeader from "@/components/layout/BaseHeader";
import NavDrawer from "@/components/layout/NavDrawer";
import AppBar from "@/components/layout/AppBar";
import BaseFooter from "@/components/layout/BaseFooter";
export default {
  data() {
    return {
      drawer: false,
      group: null,
      titleIcon: "mdi-cube-outline",
      accountIcon: "mdi-account-plus-outline",
      activeBtn: 1,
      menus: [
        {
          title: "사업안내",
          icon: "mdi-information",
          model: false,
          submenus: [
            {
              title: "사업개요",
              path: "/overview",
              icon: "mdi-information",
              class: "submenu-1 submenu-1-1 highlight-btn"
            },
            {
              title: "모집공고",
              path: "/gonggo",
              icon: "mdi-file-cloud-outline",
              class: "submenu-1 submenu-1-2 highlight-btn"
            }
          ]
        },
        {
          title: "입지안내",
          icon: "mdi-map-marker",
          model: false,
          submenus: [
            {
              title: "입지환경",
              path: "/environment",
              icon: "mdi-map-marker",
              class: "submenu-2 submenu-2-1 highlight-btn"
            },
            {
              title: "프리미엄",
              path: "/premium",
              icon: "mdi-google-circles-extended",
              class: "submenu-2 submenu-2-2 highlight-btn"
            }
          ]
        },
        {
          title: "단지안내",
          icon: "mdi-scatter-plot-outline highlight-btn",
          model: false,
          submenus: [
            {
              title: "단지 배치도",
              path: "/danji",
              icon: "mdi-scatter-plot-outline",
              class: "submenu-3 submenu-3-1 highlight-btn"
            },
            {
              title: "커뮤니티",
              path: "/community",
              icon: "mdi-nfc-tap",
              class: "submenu-3 submenu-3-2 highlight-btn"
            },
            {
              title: "조감도",
              path: "/viewmap",
              icon: "mdi-view-compact-outline",
              class: "submenu-3 submenu-3-3 highlight-btn"
            }
          ]
        },
        {
          title: "세대안내",
          icon: "mdi-file-chart",
          model: false,
          submenus: [
            {
              title: "유니트",
              path: "/unit",
              icon: "mdi-consolidate",
              class: "submenu-4 submenu-4-1 highlight-btn"
            },
            {
              title: "인테리어",
              path: "/interior",
              icon: "mdi-cube-send",
              class: "submenu-4 submenu-4-2 highlight-btn"
            },
            {
              title: "E-모델하우스",
              path: "/vr",
              icon: "mdi-virtual-reality",
              class: "submenu-4 submenu-4-3 highlight-btn"
            }
          ]
        },
        {
          title: "고객센터",
          icon: "mdi-face-agent",
          model: false,
          submenus: [
            {
              title: "관심고객등록",
              path: "/interest",
              icon: "mdi-account-plus-outline",
              class: "submenu-5 submenu-5-1 highlight-btn"
            },
            {
              title: "홍보관안내",
              path: "/information",
              icon: "mdi-floor-plan",
              class: "submenu-5 submenu-5-2 highlight-btn"
            }
          ]
        }
      ],
      fav: true,
      menu: false,
      message: false,
      hints: true
    };
  },
  watch: {
    group() {
      this.drawer = false;
    }
  },
  components: {
    BaseHeader,
    BaseFooter,
    NavDrawer,
    AppBar
  }
};
</script>

<style scoped>
.menus {
  background-color: white;
}
.menu-container {
  width: 680px;
}
.submenu-1,
.submenu-2,
.submenu-3,
.submenu-4,
.submenu-5 {
  width: 155px;
  padding-left: 0px;
  /* border-left: 1px solid; */
}

.submenu-1:hover,
.submenu-2:hover,
.submenu-3:hover,
.submenu-4:hover,
.submenu-5:hover {
  border-left: solid 1px;
  border-left-color: yellowgreen;
}

.submenu-4 {
  margin-left: 10px;
}
.submenu-5 {
  margin-left: 10px;
}
</style>



