        }
      ],
      dialog: false,
      popupAnymoreToday: false
    };
  },
  mounted() {
    let noPopupToday = localStorage.getItem("noPopup");
    let today = this.getToday();
    if (noPopupToday && noPopupToday === today) {
    } else {
      setTimeout(() => {
        this.dialog = true;
      }, 1000);
    }
  },
  methods: {
    closePopup() {
      if (this.popupAnymoreToday) {
        let today = this.getToday();
        localStorage.setItem("noPopup", today);
      }
      this.dialog = false;
    },
    getToday() {
      return new Date()
        .toJSON()
        .slice(0, 10)
        .replace(/-/g, "");
    }
  }
