<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
#nprogress .bar {
  background: #6ea75d;

  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;

  width: 100%;
  height: 5px;
}

#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 100;
  top: 35%;
  right: 47%;
  /* transform: translate(-50%, -50%); */
}

#nprogress .spinner-icon {
  width: 40px;
  height: 40px;

  border: solid 5px transparent;
  border-top-color: #6ea75d;
  border-left-color: #6ea75d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}
</style>



